import { Component } from '@angular/core';
import { IonicPage, NavController, ModalController, LoadingController, AlertController, NavParams } from 'ionic-angular';
import { GlobalVarsProvider } from '../../providers/global-vars/global-vars';
import { AlertServiceProvider } from '../../providers/alert-service/alert-service';
import { ClientdataProvider } from '../../providers/clientdata/clientdata';
import { ValidatorProvider } from '../../providers/validator/validator';
import { ContactsProvider } from '../../providers/contacts/contacts';
//import { BarcodeScanner } from '@ionic-native/barcode-scanner';
import Format from 'string-format';

@IonicPage()
@Component({
  selector: 'page-withdraw',
  templateUrl: 'withdraw.html',
})
export class WithdrawPage {
  isChange: number = 1;
  error: number = 0;
  //objectblock: any = { username: '', agent_id:'', account_from: '', account_to: '', lang: '', amount: '', type: 'MWallet' };
  objt: any = { account_to: '', lang: '' }
  accounts: any = [];
  langs: any = {};
  objectblock: any = { agent_commission: '', username: '',agentcommission:'', agent_id: '', account_from: '', account_to: '', lang: '', amount: '', type: 'MWallet' };
  constructor(public contactsProvider: ContactsProvider, public validator: ValidatorProvider, private clientdata: ClientdataProvider, public navCtrl: NavController, public navParams: NavParams,
    public modalCtrl: ModalController,
    public globalVars: GlobalVarsProvider, public loadingController: LoadingController, public alertService: AlertServiceProvider, public alertCtrl: AlertController) {
    this.langs = this.globalVars.doTranslate(this.globalVars.getLang());
    this.objectblock.username = this.globalVars.Username;
    this.accounts = globalVars.linked_acc;
    this.objectblock.account_from = this.accounts[0].acc_number;
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad WithdrawPage');
  }

  switch(any) {
    this.isChange = any;
  }

  submit() {
    if (this.objectblock.account_from == null || this.objectblock.account_from == "") {
      this.error = 1;
    }
    else if (this.objectblock.account_to == undefined || this.objectblock.account_to == "") {
      this.error = 2;
    }
    else if (this.objectblock.amount === undefined || this.objectblock.amount === "" || this.objectblock.amount === undefined || this.objectblock.amount != this.globalVars.cleanDigit(this.objectblock.amount)) {
      this.error = 3;
    }
    else {
      if (this.isChange == 1) {

        let message = "Do you want to withdraw TZS " + this.objectblock.amount + " from Account :" + this.objectblock.account_from + " to: ATM No " + this.objectblock.account_to + "?";
        let template = "<div>" + message + "</div>";
        let obj = { body: "", template: template, ttype: "", completed: false, pageTo: '' };
        let myModal = this.modalCtrl.create('ConfirmModalPage', obj);
        myModal.present();
        myModal.onDidDismiss(data => {
          if (data) { this.request(); }
        });
      } else {
        this.enq();
      }
    }
  }

 /* scanQR() {
    console.log("Scanning QR");
    this.barcodeScanner.scan().then((barcodeData) => {
      let barcodeResponse = barcodeData.text;
      this.objectblock.account_to = barcodeResponse;
    }, (err) => { });
  }*/
  ionViewDidEnter() {
    this.globalVars.reload();

  }
  goHome() {
    this.navCtrl.setRoot( 'MainPage' );
  }

  enq() {

    this.objt.account_to = (this.objectblock.account_to).toUpperCase();
    this.objectblock.agent_id = (this.objectblock.account_to).toUpperCase();
    this.objt.lang = this.globalVars.getLang();
    console.log(this.objt);
    this.alertService.showLoading(this.langs.submitting_txt);
    this.clientdata.agent_inq(this.objt)
      .subscribe(data => {
        this.objectblock.account_to = data.account;
        this.objectblock.agent_commission = data.agent_commission;

        console.log(this.objectblock.agent_commission,"8888888888888888888");
        this.alertService.dismiss();
        if (data.length != 0) {
          console.log(data);
          if (!data.error) {
            this.objectblock.agent_commission = data.commissionaccount;
            let message = Format(this.langs.withdraw_agent_meso, this.objectblock, data);
            let template = "<div>" + message + "</div>";
            let obj = { body: "", template: template, ttype: "", completed: false, pageTo: '' };
            let myModal = this.modalCtrl.create('ConfirmModalPage', obj);
            myModal.present();
            myModal.onDidDismiss(data => {
              if (data) { this.request(); }
            });
          } else if (data.error) {
            this.alertService.errorPop(data.message);
            this.navCtrl.pop();
          } else {
            this.alertService.errorPop(this.langs.tech);
            this.navCtrl.pop();
          }
        } else {
          this.alertService.myLoadingDismiss();
          this.alertService.errorPop(this.langs.tech);
          this.navCtrl.pop();
        }
      }, err => {
        console.log(err);
        console.log(err);
        this.alertService.dismiss();
        this.alertService.errorPop(this.langs.tech);
        this.navCtrl.pop();
      });
  }




  request() {
    console.log( "Withdraw.........................: " ,this.objectblock );
    this.alertService.showLoading(this.langs.submitting_txt);
    this.clientdata.withdrawal(this.objectblock)
      .subscribe(data => {
        data = this.globalVars.testdec(data);
        console.log(data)
        if (data.length != 0) {
          this.alertService.dismiss();
          if (!data.error) {
            this.alertService.errorPop(data.message);
            this.navCtrl.pop();
          } else if (data.error) {
            this.alertService.errorPop(data.message);
            this.navCtrl.pop();
          } else {
            this.alertService.errorPop(this.langs.tech);
            this.navCtrl.pop();
          }
        } else {
          this.alertService.dismiss();
          this.alertService.errorPop(this.langs.tech);
          this.navCtrl.pop();
        }
      }, err => {
        console.log(err);
        this.alertService.dismiss();
        this.alertService.errorPop(this.langs.tech);
        this.navCtrl.pop();
      });
  }
  back() {
    this.navCtrl.pop();
  }
}



